import axios from 'axios';
import { tip } from './utilities';

// Request failed with status code
const errorHandle = (status, message) => {
  switch (status) {
    // 400: 
    case 400:
      tip(message);
      break;
    // 401: 
    case 401:
      tip('');
      alert('');
      // 
      sessionStorage.removeItem('token');
      sessionStorage.removeItem('persist:root');
      sessionStorage.removeItem('name')
      sessionStorage.removeItem('no')
      window.location = (`${window.location.origin}/party-games`);
      break;
    case 402:
      tip(' ');
      alert(' ');
      sessionStorage.removeItem('token');
      sessionStorage.removeItem('persist:root');
      sessionStorage.removeItem('name')
      sessionStorage.removeItem('no')
      window.location = (`${window.location.origin}/party-games`);
      break;            
    case 403:
    case 406:   
     alert("");
     break;
    // case 404:
    // 
    default:
      console.log(`${message}`);
  }
}

// Axios instance
const instance = axios.create({
  baseURL: process.env.REACT_APP_URL
});



// request interceptors
instance.interceptors.request.use(
  (config) => {
    let token = sessionStorage.getItem('token');
    if (token) {
      config.headers.authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject();
  }
);

// response interceptors
instance.interceptors.response.use(
  (response) => {
    if(!!response){//  reflashToken 
      if(!!response.data){
        if(!!response.data.reflashToken){
          sessionStorage.setItem("jwtKey", response.data.reflashToken);
        }
      }
    }
    return response
  },
  (error) => {
    const { response } = error;

    if (response) {
      //  request  response error
      errorHandle(response.status, response.data.error);
      return Promise.reject(error);
    } else {
      //  request  response
      if (!window.navigator.onLine) {
        // 
        tip('');
      } else {
        // 
        return Promise.reject(error);
      }
    }
  }
)

export const cyproPlugin = axios.create({
  baseURL: 'https://localhost:9991',
  headers: { 'Content-Type': 'text/plain' }
});

export const cyproChtPlugin = axios.create({
  baseURL: 'http://localhost:61161',
  headers: { 'Content-Type': 'text/plain' }
});

export default instance;
