import axios from './https';

/**
 * 
 * @returns 
 */
export const choiceNumApi = async (num) => {
  
  return await axios
    .post('api/bingo-admin/choice-num/'+num, {})
    .then((response) => {
      if (response) {
        return response.data;
      } else {
        return false;
      }
    })
    .catch((err) => {
      return err.response;
    });
    
};

